var plugins = require('../../static/common/plugin.js');

import centerpic from '../../components/poatdetails/centerdetails.vue'
import centervideo from '../../components/poatdetails/centervideo.vue'

import api from '../../api/api.js'
export default {
	created() {
		var that = this;
		console.log(that.$eventHub)
		that.$eventHub.$on('is_redbao', (e) => {
			if (e == true) {
				that.shadowFn = true
				that.redBaoFn = true
			}
		})
		// console.log(that.$eventHub.$on('is_redbao'))
	},
	onLoad(e) {
		//alert(88888)

		var that = this;
		this.news_id = e.news_id









		//alert(this.life_id)

		if (uni.getSystemInfoSync().screenHeight >= 812) {
			that.judiflag = true;
		} else {
			that.judiflag = false;
		}

		
		
		//如果url中存在uid,则此链接为分享链接，需留记录
		      if(e.uid){
		          api.clickshare({
		              'type' : 'center',
		              'news_id' :  e.news_id,
		              'uid' : e.uid
		          }, function(res) {
		              console.log(res)
		          })
		      }
	},
	components: {
		centerpic,
		centervideo
	},
	onShow() {
		var that = this
		if(that.news_id > 0) {
			that.getPostDetails()
		}
		that.UxLocation = api.__CFG.UxLocation



	},
	data() {
		return {
			type: 0,
			shadowFn: false,
			redBaoFn: false,
			judiflag: false,
			postdetails: {},
			stickTiem: '', // 置顶剩余时间
			mediaPic: [],
			comments: [],
			endTime: '', // 报名剩余时间
			UxLocation: api.__CFG.UxLocation,
			wxtitle: '详情',
			loadZhaze: false,
			plugins: plugins,
		}
	},
	methods: {
		colosePoP() {
			this.shadowFn = false
			this.redBaoFn = false
		},
		getPostDetails() {
			if (this.news_id > 0) {
				api.tcinfcenterdetail({
					'news_id': this.news_id
				}, res => {
					if (res.error == 0) {
						this.postdetails = res.data.detail
						this.comments = res.data.items
					} else {
						uni.showToast({
							icon: 'none',
							title: res.message,
							duration: 1500
						})






						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							})
						}, 1500)

					}
				})
			}

		},
	}
}
